@media (prefers-color-scheme: dark) {
  body {
    --background-color: #000;
    --text-color: #efefef;
    --border-color: rgb(65, 60, 60);
  }
  button {
    background-color: #131314;
    color: #007aff;
  }
}

@media (prefers-color-scheme: light) {
  body {
    --background-color: #fff;
    --text-color: #000;
    --border-color: rgb(219, 217, 217);
  }
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
  color: var(--text-color);
  --up-color: #16c784;
  --down-color: #ea3943;
}

input {
  background: none;
  outline: none;
  border: none;
  border-bottom: 1px solid var(--border-color);
  font-size: 1em;
  color: var(--text-color);
  width: 100%;
  padding: 4px 8px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flow-root;
  min-height: 100vh;
  text-align: center;
  width: 100%;
  /* padding: 0px 8px; */
  box-sizing: border-box;
  padding-bottom: 3em;
}

button {
  /* background: rgb(95, 127, 216); */
  /* color: var(--text-color); */
  border: none;
  font-size: 1em;
  border-radius: 4px;
  padding: 4px 8px;
  /* font-weight: bold; */
  /* color: #fff; */
  /* border: 1px solid #1fe230; */
  /* background: transparent; */
  transition: all .1s ease-in-out;
}

button:hover {
  translate: 0px 2px;
}

button > svg.icon-tabler {
  vertical-align: text-top;
  margin-right: 0.2rem;
  width: 1em;
  height: 1.1em;
}

button:disabled {
  border-color: #b6b6b6;
}

button.danger {
  color: var(--down-color);
  font-weight: bold;
}

